.App {
  font-family: sans-serif;
  text-align: center;
}
*:not(.fas):not(.far) {
  font-family: Arial, Helvetica, sans-serif !important;
}
nav {
  /* background-color: rgba(18, 0, 77, 0.9); */
  backdrop-filter: blur(3.5px);
  /* background-color: rgba(0, 0, 0, 0) */
  background-image: radial-gradient(ellipse at bottom, rgba(0, 0, 120, 0.98), rgba(0, 0, 0, 0.98))
}
.nav-item,
.dropdown,
.dropdown-item {
  transition: 230ms ease-in-out;
}
.nav-item.active{
  /* background-color: #61dafb; */
  border-bottom: 3px solid #61dafb;
  /* padding-bottom: 15px; */
}
.nav-item.active .nav-link {
  color: #61dafb !important;
}
.dropdown-item.active {
  background-color: #61dafb !important;
  color: black;
}
.dropdown-item:active {
  background-color: #61dafb !important;
  color: black;
}
.dropdown {
  vertical-align: middle;
}
.dropdown-toggle {
  display: inline-block;
  vertical-align: middle;
}
.vh-60 {
  height: 60vh;
}
.min-vh-60 {
  min-height: 60vh;
}
.min-vh-40 {
  min-height: 40vh;
}
.min-vh-30 {
  min-height: 30vh;
}
.vh-40 {
  height: 40vh;
}
.row {
  width: 100%;
  margin: 0;
}
.imptSection {
  border: 5px solid white;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  text-align: center;
}
.fitImg {
  width: 80%;
  height: auto;
}
.fitSmallImg {
  width: 70% !important;
  height: auto;
}
.font-20 {
  font-size: 20px;
}
.font-18 {
  font-size: 18px;
}
.font-17 {
  font-size: 17px;
}
.font-16 {
  font-size: 16px;
}
.py-7 {
  padding-top: 7vmax;
  padding-bottom: 7vmax;
}
.scroll-down {
  font-size: 30px;
  text-align: center;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.7);
}

html {
  scroll-behavior: smooth;
}
.sectionHeader {
  border-bottom: 1px solid black;
  width: 40%;
  margin-left: auto;
  margin-right: auto;
}
.PageTemplateClass {
  background-color: #f2f7ff;
}

.responsive-poster {
  width: 35%;
}
@media screen and (max-width: 768px) {

  .forceTopMd {
    margin-top: 0px !important;
  }
  .forceBottomMd {
    margin-bottom: 0px !important;
  }
  .responsive-poster {
    width: 55% !important;
  }
}
@media screen and (max-width: 992px) {
  .custom-xlg-dnone {
    display: none;
  }
  

}
table, tr, td {
  border: 1px solid gray;
  border-collapse: collapse;
}
table {
  width: 100%;
}
.w-70 {
  width: 70%;
}

.w-30 {
  width: 30%;
}
body {
  transition: 180ms ease-in-out;
}
.imptText {
  color: #050505;
}
.glassCard {
  background-color: rgba(255, 255, 255, 0.55);
  border-radius: 10px;
  backdrop-filter: blur(2px)
}
.gradient-background-1 {
  background-image: linear-gradient(to bottom right, #bdf3ff, #e0bdff) !important;
}
.glassCard {
  box-shadow: 0 10px 15px 2px rgba(0, 0, 0, 0.15);
}
.gradient-background-2 {
  background-image: linear-gradient(to top left, rgba(219, 248, 255, 0.5), rgba(238, 219, 255, 0.5))
}
.gradient-background-2.glassCard {
  box-shadow: 0 10px 15px 2px rgba(0, 0, 0, 0.5);
}
/*
.bottomGrayBorder {
  border-bottom: 1px solid rgba(0, 0, 0, 0.4)
} */
.h-40 {
  height: 40%;
}
.h-60 {
  height: 60%;
}
.darkDivider {
  border: 0.1px solid rgba(0, 0, 0, 0.15);
}
.text-paragraph {
  font-size: 18px;

}
.selected {
  background-color: rgba(97, 218, 251, 0.5)
}
.footnoteCitation {
  font-size: 12px;
}
.fit-full-img {
  width: 100%;
}